
.Jumbotron {
    height: 20; 
    clear: both; 
    padding-top: 5;
    font-size: 18px;
    text-align: left;
}
a {
    -webkit-tap-highlight-color: transparent;
    text-decoration: none;
   }.button {
    display: inline-block;
    max-width: 300px;
    margin-top: 50px;
    border: 0;
    padding: 0 18px;
    text-align: left;
    width: 100%;
    height: 37px;
    border-radius: 4px;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: rgba(0, 0, 0, 0.84) !important;
    fill: rgba(0, 0, 0, 0.84) !important;
    box-shadow: 0 1px 7px rgba(0, 0, 0, 0.05);
    font: inherit;
    outline: none;
   }.button .svgIcon {
    vertical-align: middle;
    fill: rgba(0, 0, 0, 0.54);
    padding-right: 4px;
    height: 37px;
    display: inline-block;
   }

   .errorText {
    margin: 0 0 0 0.25em;
    padding: 0;
    color: red;
    font-style: bold;
    font-size: 1.0rem;
  }
